body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: whitesmoke;
  font-family: "Staatliches", cursive;
}

a{
  color: whitesmoke;
  font-size: 30px;
  text-decoration: none;
}
a:hover {
  color: whitesmoke;
  font-size: 30px;
  text-decoration: none;
}

.App {
  background-image: url("http://www.cogssandakan.com/News/img/basketball.jpg");
  background-size: cover;
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.intro, .enter-time, .enter-name-A, .enter-name-B, .scoreboard, .page-not-found {
  height: 100vh;
}

/* clock */

.position_all {
  display: inline-flex;
  flex-direction: column;
  background: #cc000000;
}
.position_quater {
  display: inline-flex;
  justify-content: space-between;
  margin-bottom: 20px;
  font-size: 2em;
  margin-top: 20px;
  text-align: center;
}
#quater_change {
  padding : 10px 20px;
  border: 3px solid white;
  border-radius: 5px;
  color: white;
}
.position_timer {
  display: flex;
  padding : 30px 40px;
  background: black;
  border-radius: 5px;
background-color: whitesmoke;
}
.timer_but, .timer_but1 {
  padding: 10px;
  margin-top: 10px;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
  background: #dbe4e6;
  
}
.timeoutstyle {
  cursor: pointer;
  width: 60%;
  height: 60px;
  background-color:black;
  color: white;
  padding: 15px 0px;
  border-radius: 4px;
  margin-right: 10px;
}
.changtime {
  padding: 0px 5px;
  border-radius: 3px;
  background: #ff5757;
  margin: 3px;
  color: white;
  cursor: pointer;
}
.timer_but1 {
  width: 95%;
  text-align: center;
}
#showInput {
  display: flex;
  align-items: center;
}
#InputId {
  margin-left: 10px;
  text-align: center;
  font-size: 0.7em;
  width: 45px;
  padding: 0;
}

#clock .display, 
#clock_1 .display_1,
#clock_2 .display_2{
text-align:center;
border-radius:6px;
position:relative;
  padding: 10px;
}
#clock_2 .display_2{
  margin: 10px 0px;
  display: flex;
  font-size: 2.3em;
  padding: 7px 2px;
}


#clock_1.light .digits_1 div span,
#clock_2.light .digits_2 div span{
background-color:#272e38;
border-color:#272e38;	
}

#clock.light .digits div span{
background-color: white;
border-color: white;	
}

#clock.light .digits div.dots:before,
#clock.light .digits div.dots:after,
#clock_1.light .digits_1 div.dots:before,
#clock_1.light .digits_1 div.dots:after,
#clock_2.light .digits_2 div.dots:before,
#clock_2.light .digits_2 div.dots:after{
background-color:#272e38;
}


#clock.light .display{
background-color: #ff5757;
box-shadow:0 1px 1px rgba(0,0,0,0.08) inset, 0 1px 1px #fafafa;
}

#clock_1.light .display_1{
background-color:#dddddd;
box-shadow:0 1px 1px rgba(0,0,0,0.08) inset, 0 1px 1px #fafafa;
}
#clock_2.light .display_2 {
  display: flex;
  background-color: white;
  justify-content: space-around;
  align-items: center;
}
#clock_1.light .display_1{
  margin-right: 10px;
}


#clock .digits div,
#clock_1 .digits_1 div,
#clock_2 .digits_2 div{
text-align:left;
position:relative;
width: 26px;
height:50px;
display:inline-block;
margin:0 4px;
}

#clock .digits div span,
#clock_1 .digits_1 div span,
#clock_2 .digits_2 div span{
opacity:0;
position:absolute;

-webkit-transition:0.25s;
-moz-transition:0.25s;
transition:0.25s;
}

#clock .digits div span:before,
#clock .digits div span:after,
#clock_1 .digits_1 div span:before,
#clock_1 .digits_1 div span:after,
#clock_2 .digits_2 div span:before,
#clock_2 .digits_2 div span:after{
content:'';
position:absolute;
width:0;
height:0;
border:5px solid transparent;
}

#clock .digits .d1,
#clock_1 .digits_1 .d1,
#clock_2 .digits_2 .d1{			height:5px;width:16px;top:0;left:5px;}
